export default {
    namespaced: true,
    state: {
        filter: null,
    },
    getters: {
        filter: (state) => state.filter,
    },
    mutations: {
        SET_FILTER(state, payload) {
            state.filter = payload;
        },
    },
    actions: {
        ActionSetFilter({ commit }, payload) {
            commit('SET_FILTER', payload);
        }
    },
};