<template>
  <q-layout view="hHh Lpr lFf">
    <q-header reveal>
      <q-toolbar class="bg-secondary">
        <q-btn
          flat
          dense
          round
          @click="leftDrawer = !leftDrawer"
          aria-label="Menu"
          icon="menu"
          v-if="!searchView"
          :size="isMobile ? '1rem' : ''"
        />
        <q-toolbar-title
          :class="isMobile ? 'text-center q-py-sm' : 'q-px-md'"
          v-if="!searchView"
        >
          <img
            :src="require('../assets/logo_flow.svg')"
            :height="isMobile ? '42' : '36'"
            :class="'q-pt-sm'"
        /></q-toolbar-title>
        <q-intersection transition="slide-left" v-show="searchView">
          <q-input
            color="white"
            filled
            class="q-my-xs"
            v-model="searchValue"
            dark
            dense
            placeholder="Buscar..."
            :style="`width: ${$q.screen.width - 23}px`"
            v-if="searchView"
            debounce="300"
          >
            <template v-slot:prepend>
              <q-icon name="eva-search-outline" class="q-pr-sm" />
            </template>
            <template v-slot:append>
              <q-btn
                flat
                round
                dense
                icon="eva-close"
                @click="(searchView = false), ActionSetFilter('')"
              />
            </template>
          </q-input>
        </q-intersection>

        <q-btn
          flat
          dense
          round
          :size="isMobile ? '1rem' : ''"
          v-if="!searchView && isMobile"
          icon="eva-search-outline"
          @click="searchView = true"
        />
      </q-toolbar>
    </q-header>

    <q-footer reveal class="bg-secondary" v-if="isMobile">
      <q-tabs v-model="tab" :breakpoint="0" align="justify">
        <q-route-tab
          name="magazine"
          icon="mdi-book-open-page-variant-outline"
          to="/magazine"
        />
        <q-route-tab name="gameshow" icon="mdi-gift-outline"  to="/magazine"/>
        <q-route-tab name="qrcode" icon="mdi-qrcode" to="/qrcode" />
        <q-route-tab name="avaliar" icon="mdi-thumb-up-outline"  to="/magazine" />
        <q-route-tab name="preferences" icon="mdi-tune"  to="/auth"/>
      </q-tabs>
    </q-footer>

    <q-drawer
      v-model="leftDrawer"
      show-if-above
      bordered
      content-class="bg-grey-2"
    >
      <main-menu />
      <user-menu-mobile />
    </q-drawer>
    <q-page-container>
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script>
import MagazineService from "@/mixins/MagazineService";
import MainMenu from "@/components/MainMenu.vue";
import UserMenuMobile from "@/components/UserMenuMobile.vue";
export default {
  name: "LayoutMain",
  mixins: [MagazineService],
  components: { MainMenu, UserMenuMobile },
  data() {
    return {
      searchView: false,
      leftDrawer: false,
      tab: "magazine",
      searchValue: null,
    };
  },
  watch: {
    searchValue() {
      this.ActionSetFilter(this.searchValue);
    },
    filter() {
      if (this.filter == "") this.searchValue = null;
    },
  },
  computed: {
    isMobile() {
      return (
        (this.$q.platform.is.mobile || this.$q.screen.name == "xs") &&
        this.$q.screen.height > this.$q.screen.width
      );
    },
  },
  created() {},
};
</script>

